/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
menu,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu,
ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font-family: "Roboto", sans-serif;
}

.card {
  height: 189px;
  display: flex;
  position: relative;
  margin: 20px 0;
}
.card__image {
  padding: 21px 0;
  padding-right: 20px;
}
.card__adxLogo {
  display: block;
  position: absolute;
  right: 0;
}
.card__content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-right: 5px;
  padding-bottom: 5px;
}
.card__body {
  margin-top: 17px;
}
.card__body--title {
  font-size: 18px;
  font-weight: 700;
  line-height: 23px;
  color: initial;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.card__body--link {
  font-size: 12px;
  font-weight: 300;
  line-height: 18px;
  color: #a5a5a5;
}
.card__body--description {
  font-size: 13px;
  font-weight: 400;
  line-height: 18px;
  color: #868686;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.card__footer {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card__footer--left {
  display: flex;
}
.card__footer--left > *:not(:last-child) {
  margin-right: 5px;
}
.card__footer--sponsor-name {
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  color: #1c7ddc;
}
.card__footer--text {
  align-self: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #a5a5a5;
}
.card__footer--goto {
  height: 30px;
  width: 30px;
}